import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import xinwenleibie from '@/views/modules/xinwenleibie/list'
    import forum from '@/views/modules/forum/list'
    import news from '@/views/modules/news/list'
    import aboutus from '@/views/modules/aboutus/list'
    import yonghu from '@/views/modules/yonghu/list'
    import discussxinwenxinxi from '@/views/modules/discussxinwenxinxi/list'
    import xinwenxinxi from '@/views/modules/xinwenxinxi/list'
    import discussxuexitiandi from '@/views/modules/discussxuexitiandi/list'
    import xuexitiandi from '@/views/modules/xuexitiandi/list'
    import config from '@/views/modules/config/list'
    import xuexileixing from '@/views/modules/xuexileixing/list'
    import newstype from '@/views/modules/newstype/list'


//2.   
export const routes = [{
    path: '/',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center', affix: true}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/xinwenleibie',
        name: '',
        component: xinwenleibie
      }
      ,{
	path: '/forum',
        name: '',
        component: forum
      }
      ,{
	path: '/news',
        name: '',
        component: news
      }
      ,{
	path: '/aboutus',
        name: '',
        component: aboutus
      }
      ,{
	path: '/yonghu',
        name: '',
        component: yonghu
      }
      ,{
	path: '/discussxinwenxinxi',
        name: '',
        component: discussxinwenxinxi
      }
      ,{
	path: '/xinwenxinxi',
        name: '',
        component: xinwenxinxi
      }
      ,{
	path: '/discussxuexitiandi',
        name: '',
        component: discussxuexitiandi
      }
      ,{
	path: '/xuexitiandi',
        name: '',
        component: xuexitiandi
      }
      ,{
	path: '/config',
        name: '',
        component: config
      }
      ,{
	path: '/xuexileixing',
        name: '',
        component: xuexileixing
      }
      ,{
	path: '/newstype',
        name: '',
        component: newstype
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
